import {createCustomElement} from '@servicenow/ui-core';
import {DropTarget, DragDropContext} from 'react-dnd';
import react from '@servicenow/ui-renderer-react';
import React from 'react';
import PropTypes from 'prop-types';
import isEqual from 'lodash/isEqual';
import {default as move} from 'lodash-move';
import get from 'lodash/get';
import differenceBy from 'lodash/differenceBy';
import {t} from 'sn-translate';
import HTML5Backend from 'react-dnd-html5-backend';
import DragAndDropItem from './subcomponents/dragAndDropItem';
import style from './dragAndDrop.scss';
import packageJson from '../package.json';

const itemTarget = {
	drop() {}
};

class DragAndDrop extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			liveText: ''
		};
	}

	moveItem = (id, atIndex) => {
		const {index} = this.findItem(id);
		const {
			properties: {
				onDataChange,
				options: {data}
			}
		} = this.props;
		const nextData = move(data, index, atIndex);

		if (isEqual(data, nextData)) {
			return;
		}
		onDataChange(nextData);
	};

	dragAndDropState = state => {
		const {
			properties: {dragAndDropState}
		} = this.props;
		dragAndDropState(state);
	};

	setList = list => {
		this.list = list;
	};

	findItem = id => {
		const {
			properties: {
				options: {data}
			}
		} = this.props;
		const item = data.filter(item => item.id === id)[0];

		return {item, index: data.indexOf(item)};
	};

	componentWillReceiveProps(nextProps) {
		const nextData = get(nextProps, 'properties.options.data');
		const data = get(this.props, 'properties.options.data');
		const title = get(this.props, 'properties.options.title');

		let liveText = '';
		if (nextData.length > data.length) {
			const newItem = differenceBy(nextData, data, 'id')[0];
			const {displayValue} = newItem;
			liveText = t('List Item {0} has been added to {1}', displayValue, title);
		} else if (nextData.length < data.length) {
			const deletedItem = differenceBy(data, nextData, 'id')[0];
			const {displayValue} = deletedItem;
			liveText = t(
				'List Item {0} has been deleted from {1}',
				displayValue,
				title
			);
		}

		this.setState({liveText});
	}

	componentDidUpdate() {
		const {liveText} = this.state;
		if (liveText && this.list) {
			this.list.focus();
		}
	}

	render() {
		const {
			properties: {
				options: {data, title},
				id,
				emptyStateMessage
			},
			connectDropTarget,
			onDragEvent
		} = this.props;
		const hasData = data.length > 0;
		const {liveText} = this.state;
		const listTitle = liveText ? `${title} - ${liveText}` : title;

		return (
			<div className="sn-drag-drop">
				{hasData &&
					connectDropTarget(
						<ul
							role="menu"
							aria-label={listTitle}
							tabIndex="-1"
							ref={this.setList}
							className="sn-drag-drop-ul">
							{data.map(item => {
								const props = {
									key: item.id,
									parentKey: id,
									options: {
										moveItem: this.moveItem,
										findItem: this.findItem,
										dragAndDropState: this.dragAndDropState,
										dragEvent: onDragEvent
									},
									model: {
										id: item.id,
										item: item,
										displayValue: item.displayValue
									}
								};
								return <DragAndDropItem key={`dnd-item${id}`} {...props} />;
							})}
						</ul>
					)}
				{!hasData && (
					<div className="sn-empty-state">
						<div className="sn-empty-state-row">{emptyStateMessage}</div>
					</div>
				)}
			</div>
		);
	}
}

DragAndDrop.propTypes = {
	connectDropTarget: PropTypes.func.isRequired,
	properties: PropTypes.shape({
		options: PropTypes.object.isRequired,
		onDataChange: PropTypes.func.isRequired,
		dragAndDropState: PropTypes.func.isRequired,
		id: PropTypes.string.isRequired,
		emptyStateMessage: PropTypes.string
	})
};

const context = DragDropContext(HTML5Backend)(
	DropTarget(props => props.properties.id, itemTarget, connect => ({
		connectDropTarget: connect.dropTarget()
	}))(DragAndDrop)
);

export const findFirstTabbableElement = (root = document) => {
	if (!root) return false;

	const selector = ['[tabindex]'];
	let elements = [...root.querySelectorAll(selector)];

	if (root.shadowRoot) root = root.shadowRoot;

	return elements.find(isTabbable);
};

export const isTabbable = node => {
	const tabIndex = parseInt(node.getAttribute('tabindex'), 10);
	if (!isNaN(tabIndex) && tabIndex < 0) return false;
	return true;
};

createCustomElement('sn-drag-and-drop', {
	renderer: {
		type: react,
		view: context
	},
	properties: {
		options: {default: {data: []}},
		onDataChange: {default: () => {}},
		dragAndDropState: {default: () => {}},
		id: {default: ''},
		emptyStateMessage: {default: ''}
	},
	onConnect(host) {
		if (!window.listComponentRegistry) {
			window.listComponentRegistry = new WeakMap();
		}
		window.listComponentRegistry.set(host, packageJson.version);
	},
	onDisconnect(host) {
		if (window.listComponentRegistry) {
			window.listComponentRegistry.delete(host);
		}
	},
	onBootstrap(host) {
		findFirstTabbableElement(host.shadowRoot).focus();
	},
	styles: `${style}`
});
