import React from 'react';
import PropTypes from 'prop-types';
import {DragSource, DropTarget} from 'react-dnd';
import {t} from 'sn-translate';
import '@servicenow/now-icon';

const itemSource = {
	beginDrag(props) {
		const {options, model} = props;
		const {findItem, dragAndDropState} = options;
		const {id} = model;

		dragAndDropState({isDragging: true});

		return {id, originalIndex: findItem(id).index};
	},

	endDrag(props, monitor) {
		const {id: droppedId, originalIndex} = monitor.getItem();
		const {options} = props;
		const {moveItem, dragAndDropState} = options;
		const didDrop = monitor.didDrop();

		dragAndDropState({isDragging: false});

		if (!didDrop) {
			moveItem(droppedId, originalIndex);
		}
	}
};

const itemTarget = {
	canDrop() {
		return false;
	},

	hover(props, monitor) {
		const {id: draggedId} = monitor.getItem();
		const {model, options} = props;
		const {findItem, moveItem} = options;
		const {id: overId} = model;

		if (draggedId !== overId) {
			const {index: overIndex} = findItem(overId);
			moveItem(draggedId, overIndex);
		}
	}
};

class DragAndDropItem extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			isChangingOrder: false
		};
	}

	setContent = content => {
		this.content = content;
	};

	stopChangingOrder() {
		if (this.state.isChangingOrder) this.setState({isChangingOrder: false});
	}

	handleKeyEvent(evt) {
		if (this.state.isChangingOrder) {
			this.handleChangeOrderEvt(evt);
			return;
		}

		switch (evt.key) {
			case 'Enter':
				evt.preventDefault();

				this.setState({
					isChangingOrder: true
				});

				break;
			default:
				break;
		}
	}

	handleChangeOrderEvt(evt) {
		const {
			model: {id},
			options: {moveItem, findItem}
		} = this.props;

		evt.preventDefault();

		switch (evt.key) {
			case 'Enter':
			case 'Escape':
				this.stopChangingOrder();
				break;
			case 'ArrowDown':
				moveItem(id, findItem(id).index + 1);
				break;
			case 'ArrowUp': {
				const newIndex = findItem(id).index - 1;
				if (newIndex >= 0) {
					moveItem(id, newIndex);
				}
				break;
			}
			default:
				break;
		}
	}

	componentDidUpdate() {
		if (this.state.isChangingOrder) this.content.focus();
	}

	render() {
		const {
			model,
			isDragging,
			connectDragSource,
			connectDropTarget
		} = this.props;
		const {displayValue} = model;
		const itemTitle = t('Edit List Item - {0}', displayValue);
		const opacity = isDragging ? 0 : 1;

		return connectDragSource(
			connectDropTarget(
				<li style={{opacity}} aria-label={itemTitle} role="menuitem">
					<div
						className="sn-drag-drop-node"
						tabIndex="0"
						onKeyDown={evt => this.handleKeyEvent(evt)}
						onBlur={() => this.stopChangingOrder()}
						role="button"
						ref={this.setContent}
						title={itemTitle}
						aria-label={itemTitle}>
						<div className="sn-drag-drop-icon">
							<now-icon icon="ellipsis-v-outline" size="sm" />
						</div>
						<div className="sn-drag-drop-label">
							<span>{displayValue}</span>
						</div>
					</div>
				</li>
			)
		);
	}
}

DragAndDropItem.propTypes = {
	connectDragSource: PropTypes.func.isRequired,
	connectDropTarget: PropTypes.func.isRequired,
	isDragging: PropTypes.bool.isRequired,
	dragAction: PropTypes.func,
	options: PropTypes.object.isRequired,
	model: PropTypes.object.isRequired,
	parentKey: PropTypes.string.isRequired
};

export default DragSource(
	props => props.parentKey,
	itemSource,
	(connect, monitor) => ({
		connectDragSource: connect.dragSource(),
		isDragging: monitor.isDragging(),
		getItem: monitor.getItem()
	})
)(
	DropTarget(props => props.parentKey, itemTarget, connect => ({
		connectDropTarget: connect.dropTarget()
	}))(DragAndDropItem)
);
